@import'../global.css';.characters-panel{display:flex;flex-direction:column;gap:16px;max-height:500px;overflow-y:auto;padding-right:8px}.characters-panel::-webkit-scrollbar{width:6px}.characters-panel::-webkit-scrollbar-track{background:var(--background-color);border-radius:3px}.characters-panel::-webkit-scrollbar-thumb{background:var(--border-color);border-radius:3px}.characters-panel::-webkit-scrollbar-thumb:hover{background:var(--subtext-color)}.empty-state{text-align:center;padding:24px;color:var(--subtext-color);font-style:italic}.characters-list{display:flex;flex-direction:column;gap:12px}.char-card{background-color:var(--card-bg-color);border:1px solid var(--border-color);border-radius:8px;overflow:hidden;transition:all .2s}.char-card:hover{border-color:var(--hover-color);box-shadow:0 2px 8px rgba(0,0,0,.1)}.char-name{padding:12px 16px;cursor:pointer;display:flex;justify-content:space-between;align-items:center;background-color:rgba(0,0,0,.1)}.char-name:hover{background-color:rgba(0,0,0,.15)}.char-name-left{display:flex;align-items:center;gap:10px}.char-avatar{width:64px;height:64px;border-radius:50%;object-fit:cover;border:2px solid var(--border-color)}.char-avatar-placeholder{width:64px;height:64px;border-radius:50%;background-color:var(--background-color);border:2px solid var(--border-color);display:flex;align-items:center;justify-content:center;color:var(--subtext-color);font-size:28px}.char-name-text{font-weight:500}.collapse-icon{width:16px;height:16px;transition:transform .3s ease}.collapse-icon.rotated{transform:rotate(180deg)}.character-detail{padding:16px;border-top:1px solid var(--border-color);animation:slideDown .3s ease}@keyframes slideDown{from{opacity:0;max-height:0;overflow:hidden}to{opacity:1;max-height:500px;overflow:hidden}}.char-details{display:flex;flex-direction:column;gap:12px}.detail-item{display:flex;align-items:center;gap:8px;font-size:14px}.detail-item i{width:16px;text-align:center;color:var(--text-color)}.detail-item strong{color:var(--text-color);font-weight:600}.detail-item span{color:var(--subtext-color)}.status-highlight-yes{color:var(--green);font-weight:600}.status-highlight-no{color:var(--red);font-weight:600}.stat-bar-container{margin-top:8px}.stat-bar-label{display:flex;justify-content:space-between;align-items:center;margin-bottom:4px;font-size:13px}.stat-bar-title{display:flex;align-items:center;gap:6px;color:var(--text-color)}.stat-bar-value{color:var(--subtext-color);font-size:12px;font-weight:500}.progress-bar{height:8px;background-color:rgba(0,0,0,.2);border-radius:4px;overflow:hidden}.progress-bar-fill{height:100%;border-radius:4px;transition:width .3s ease}.magic-bar{background-color:var(--green)}.favor-bar-positive{background-color:var(--pink)}.favor-bar-negative{background-color:var(--red)}.corruption-bar{background-color:var(--purple)}.lust-bar{background-color:var(--red)}.thought-block{margin-top:12px;padding:12px;background-color:rgba(0,0,0,.1);border-radius:6px;border-left:3px solid var(--blue)}.thought-title{font-size:13px;font-weight:600;color:var(--text-color);margin-bottom:6px;display:flex;align-items:center;gap:6px}.thought-block p{font-size:13px;color:var(--subtext-color);line-height:1.4;margin:0}