@import'../global.css';.character-detail-panel{display:flex;flex-direction:column;gap:16px;min-height:400px}.character-detail-content{display:flex;flex-direction:column;gap:16px}.avatar-section{display:flex;flex-direction:column;align-items:center;gap:16px;padding:0 0 24px 0;background-color:var(--card-bg-color);border:1px solid var(--border-color);border-radius:12px;overflow:hidden}.avatar-banner{position:relative;width:100%;height:180px;overflow:hidden;background-color:var(--quote-bg-color)}.character-avatar-img{width:100%;height:100%;object-fit:contain;object-position:top center}.avatar-gradient-overlay{position:absolute;bottom:0;left:0;right:0;height:100px;background:linear-gradient(to bottom, transparent, var(--card-bg-color));pointer-events:none}.avatar-placeholder-large{width:100%;height:100%;display:flex;align-items:center;justify-content:center;font-size:64px;color:var(--subtext-color)}.change-avatar-btn{position:absolute;bottom:0;right:0;width:36px;height:36px;border-radius:50%;background-color:var(--blue);border:2px solid var(--card-bg-color);color:#fff;cursor:pointer;display:flex;align-items:center;justify-content:center;transition:all .2s;box-shadow:0 2px 8px rgba(0,0,0,.2)}.change-avatar-btn:hover{background-color:var(--blue-hover, #6d9cf3);transform:scale(1.1)}.character-title{font-size:20px;font-weight:600;color:var(--text-color)}.avatar-history{width:100%;margin-top:8px}.history-title{font-size:12px;color:var(--subtext-color);margin-bottom:8px;text-align:center}.history-list{display:flex;justify-content:center;gap:8px;flex-wrap:wrap}.history-item{position:relative;width:48px;height:48px;border-radius:8px;overflow:hidden;border:2px solid var(--border-color);cursor:pointer;transition:all .2s;background-color:var(--quote-bg-color)}.history-item:hover{border-color:var(--blue);transform:scale(1.1)}.history-item.active{border-color:var(--green);box-shadow:0 0 0 2px rgba(74,222,128,.3)}.history-item.error{display:none}.history-item img{width:100%;height:100%;object-fit:cover}.delete-avatar-btn{position:absolute;top:2px;right:2px;width:16px;height:16px;border-radius:50%;background-color:var(--red);border:1px solid var(--card-bg-color);color:#fff;cursor:pointer;display:flex;align-items:center;justify-content:center;transition:all .2s;opacity:0;z-index:10}.delete-avatar-btn i{font-size:10px}.history-item:hover .delete-avatar-btn{opacity:1}.delete-avatar-btn:hover{background-color:#e06c75;transform:scale(1.2)}.additional-info{margin-top:16px}.info-grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(150px, 1fr));gap:12px}.info-item{background-color:var(--card-bg-color);padding:12px;border-radius:8px;border:1px solid var(--border-color);font-size:.9em}.info-item strong{color:var(--text-color);margin-right:4px}.info-item span{color:var(--subtext-color)}