@import'./global.css';.page-view{animation:card-enter .5s ease-out forwards}.status-ending-completed{background:linear-gradient(135deg, #f59e0b, #fbbf24);color:#1a1a2e;font-weight:bold;animation:glow 2s ease-in-out infinite alternate}.ending-item.ending-completed{border-color:#f59e0b;background:linear-gradient(135deg, var(--card-bg-color) 0%, rgba(245, 158, 11, 0.1) 100%);box-shadow:0 2px 8px rgba(245,158,11,.2)}.ending-name.ending-name-completed{color:#f59e0b;font-weight:bold;text-shadow:0 0 8px rgba(245,158,11,.5)}.ending-completed-badge{background:linear-gradient(135deg, #f59e0b, #fbbf24);color:#1a1a2e;font-size:10px;padding:4px 8px;border-radius:4px;font-weight:bold;animation:glow 2s ease-in-out infinite alternate}@keyframes glow{from{box-shadow:0 0 5px rgba(245,158,11,.5)}to{box-shadow:0 0 20px rgba(245,158,11,.8)}}#characters-page,#archive-page,#terminal-page,#skill-list-page,#learn-skills-page,#story-page,#story-detail-page,#character-detail-page{min-height:400px}.user-stats-section{margin:16px 0}.stats-grid{display:grid;grid-template-columns:repeat(3, 1fr);gap:12px}.stat-card{display:flex;flex-direction:column;align-items:center;gap:8px;background:var(--card-bg-color);border:1px solid var(--border-color);border-radius:8px;padding:16px;text-align:center;transition:all .2s}.stat-card:hover{border-color:var(--blue);box-shadow:0 2px 8px rgba(137,180,250,.2);transform:translateY(-2px)}.stat-icon{font-size:32px}.stat-info{display:flex;flex-direction:column;gap:4px}.stat-value{font-size:24px;font-weight:700;color:var(--text-color)}.stat-value.positive{color:var(--green)}.stat-value.negative{color:var(--red)}.stat-label{font-size:12px;color:var(--subtext-color)}.skills-section{margin-top:24px}.skill-cards{display:flex;gap:16px;margin-bottom:16px}.skill-card{flex:1;background:var(--card-bg-color);border:1px solid var(--border-color);border-radius:12px;padding:20px;cursor:pointer;transition:all .2s;display:flex;flex-direction:column;align-items:center;justify-content:center;gap:12px;text-align:center}.skill-card:hover{border-color:var(--blue);box-shadow:0 4px 16px rgba(137,180,250,.2);transform:translateY(-2px)}.skill-card-icon{width:64px;height:64px;display:flex;align-items:center;justify-content:center;color:var(--blue)}.skill-card-text{display:flex;align-items:center;gap:6px}.skill-card-title{font-size:16px;font-weight:600;color:var(--text-color)}.skill-card-count{font-size:14px;color:var(--subtext-color)}.skill-list-content{margin-top:16px}.learned-skills-list{display:flex;flex-direction:column;gap:12px}.learned-skill-item{background-color:var(--card-bg-color);border:1px solid var(--border-color);border-radius:8px;padding:16px;transition:all .2s}.learned-skill-item:hover{border-color:var(--blue);box-shadow:0 4px 16px rgba(137,180,250,.15)}.skill-item-header{display:flex;align-items:center;justify-content:space-between;margin-bottom:8px}.skill-check{color:var(--green);margin-right:12px}.skill-name{flex:1;font-size:16px;font-weight:600;color:var(--text-color)}.skill-status{font-size:14px;font-weight:500;color:var(--green);background-color:rgba(74,222,128,.1);padding:4px 12px;border-radius:12px}.skill-description{font-size:14px;color:var(--subtext-color);line-height:1.4;margin-top:8px;padding-top:8px;border-top:1px solid var(--border-color)}.learn-skills-content{margin-top:8px}.story-detail-content{margin-top:20px;display:flex;flex-direction:column;gap:16px}.story-detail-card{background-color:var(--card-bg-color);border:1px solid var(--border-color);border-radius:8px;padding:20px}.story-status{display:flex;align-items:center;gap:12px;margin-bottom:20px;padding-bottom:12px;border-bottom:1px solid var(--border-color)}.story-status strong{color:var(--text-color)}.story-status span{font-size:14px;padding:6px 12px;border-radius:4px;font-weight:600;text-transform:uppercase}.status-not-started{background-color:#6a6a8a;color:#c0c0e0}.status-in-progress{background-color:#fbbf24;color:#1a1a2e}.status-completed{background-color:#4ade80;color:#1a1a2e}.status-unknown{background-color:#8888a8;color:#e0e0e0}.endings-section{margin-top:20px}.endings-title{font-size:14px;color:#8888a8;margin-bottom:12px;font-weight:500}.endings-list{display:flex;flex-direction:column;gap:16px}.ending-item{background-color:var(--card-bg-color);border:1px solid var(--border-color);border-radius:6px;padding:12px 16px;transition:all .2s}.ending-item:hover{border-color:var(--blue);box-shadow:0 2px 8px rgba(137,180,250,.2);transform:translateY(-1px)}.ending-header{display:flex;justify-content:space-between;align-items:center;margin-bottom:8px}.ending-name{font-size:14px;color:var(--text-color);font-weight:500}.ending-description{margin-top:12px;padding-top:12px;border-top:1px solid var(--border-color)}.description-title{font-size:12px;color:var(--subtext-color);margin-bottom:8px;font-weight:500}.description-content{font-size:13px;color:var(--text-color);line-height:1.4;margin:0;padding:8px 12px;background-color:var(--quote-bg-color);border-radius:4px}.ending-conditions{margin-top:12px;padding-top:12px;border-top:1px solid var(--border-color)}.conditions-title{font-size:12px;color:var(--subtext-color);margin-bottom:8px;font-weight:500}.conditions-list{display:flex;flex-direction:column;gap:6px}.condition-item{display:flex;justify-content:space-between;align-items:center;font-size:12px;padding:6px 10px;background-color:var(--quote-bg-color);border-radius:4px;cursor:pointer;transition:all .2s}.condition-item:hover{background-color:var(--card-bg-color);border:1px solid var(--blue);box-shadow:0 2px 8px rgba(137,180,250,.2);transform:translateY(-1px)}.condition-item.condition-met{background-color:rgba(74,222,128,.1);border-left:2px solid var(--green)}.condition-item.condition-met:hover{background-color:rgba(166,227,161,.2);border-color:var(--green);box-shadow:0 2px 8px rgba(166,227,161,.3)}.condition-name{color:var(--subtext-color)}.condition-status{font-weight:bold;margin-left:12px}.condition-item.condition-met .condition-status{color:var(--green)}.condition-item:not(.condition-met) .condition-status{color:var(--red)}.no-endings{margin-top:20px;padding:20px;background-color:var(--quote-bg-color);border-radius:6px;text-align:center}.no-endings-text{font-size:14px;color:var(--subtext-color);font-style:italic}.loading{text-align:center;padding:40px;color:var(--subtext-color);font-style:italic}