
.story-panel {
  display: flex;
  flex-direction: column;
  gap: 16px;
  max-height: 500px;
  overflow-y: auto;
  padding-right: 8px;
}

/* 自定义滚动条 */
.story-panel::-webkit-scrollbar {
  width: 6px;
}

.story-panel::-webkit-scrollbar-track {
  background: var(--border-color);
  border-radius: 3px;
}

.story-panel::-webkit-scrollbar-thumb {
  background: var(--grey-blue);
  border-radius: 3px;
}

.story-panel::-webkit-scrollbar-thumb:hover {
  background: var(--blue);
}

.panel-title {
  margin: 0 0 8px 0;
  font-size: 16px;
  color: var(--text-color);
  border-bottom: 1px solid var(--border-color);
  padding-bottom: 8px;
}

.empty-state {
  text-align: center;
  padding: 24px;
  color: var(--subtext-color);
  font-style: italic;
}

.story-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.story-card {
  background-color: var(--card-bg-color);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 14px;
  transition: all 0.2s;
  cursor: pointer;
}

.story-card:hover {
  border-color: var(--blue);
  box-shadow: 0 2px 8px rgba(137, 180, 250, 0.2);
  transform: translateY(-2px);
}

.story-card.completed {
  border-color: var(--green);
  background: linear-gradient(135deg, var(--card-bg-color) 0%, rgba(166, 227, 161, 0.1) 100%);
}

.story-card.in-progress {
  border-color: #fbbf24;
  background: linear-gradient(135deg, var(--card-bg-color) 0%, rgba(251, 191, 36, 0.1) 100%);
}

.story-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
  padding-bottom: 8px;
  border-bottom: 1px solid var(--border-color);
}

.story-name {
  font-size: 15px;
  font-weight: 600;
  color: var(--text-color);
}

.story-header-right {
  display: flex;
  align-items: center;
  gap: 12px;
}

.story-status {
  font-size: 11px;
  padding: 4px 8px;
  border-radius: 4px;
  font-weight: 600;
  text-transform: uppercase;
}

.story-header-right i {
  color: var(--subtext-color);
  font-size: 14px;
  transition: transform 0.2s;
}

.story-card:hover .story-header-right i {
  transform: translateX(4px);
  color: var(--text-color);
}

.story-preview {
  margin-top: 12px;
  padding-top: 12px;
  border-top: 1px solid var(--border-color);
  font-size: 13px;
}

.endings-preview {
  color: var(--subtext-color);
}

.endings-count {
  font-weight: 500;
}

.no-endings-preview {
  color: var(--subtext-color);
  font-style: italic;
}

.status-not-started {
  background-color: var(--grey-blue);
  color: var(--text-color);
}

.status-in-progress {
  background-color: #fbbf24;
  color: #1a1a2e;
}

.status-completed {
  background-color: var(--green);
  color: #1a1a2e;
}

.status-unknown {
  background-color: var(--grey-blue);
  color: var(--text-color);
}

/* 结局完成状态样式 */
.status-ending-completed {
  background: linear-gradient(135deg, #f59e0b, #fbbf24);
  color: #1a1a2e;
  font-weight: bold;
  animation: glow 2s ease-in-out infinite alternate;
}

/* 结局完成的个人线卡片样式 */
.story-card.ending-completed {
  border-color: #f59e0b;
  background: linear-gradient(135deg, var(--card-bg-color) 0%, rgba(245, 158, 11, 0.1) 100%);
  box-shadow: 0 2px 8px rgba(245, 158, 11, 0.2);
}

/* 已完成结局数量样式 */
.completed-endings-count {
  color: #f59e0b;
  font-weight: bold;
  margin-left: 8px;
}

/* 结局完成动画 */
@keyframes glow {
  from {
    box-shadow: 0 0 5px rgba(245, 158, 11, 0.5);
  }
  to {
    box-shadow: 0 0 20px rgba(245, 158, 11, 0.8);
  }
}

.endings-section {
  margin-top: 12px;
}

.endings-title {
  font-size: 12px;
  color: var(--subtext-color);
  margin-bottom: 8px;
  font-weight: 500;
}

.endings-list {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.ending-item {
  background-color: var(--card-bg-color);
  border: 1px solid var(--border-color);
  border-radius: 6px;
  padding: 6px 10px;
  transition: all 0.2s;
}

.ending-item:hover {
  border-color: var(--blue);
  box-shadow: 0 2px 8px rgba(137, 180, 250, 0.2);
  transform: translateY(-1px);
}

.ending-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 6px;
}

.ending-name {
  font-size: 12px;
  color: var(--text-color);
  font-weight: 500;
}

.ending-conditions {
  margin-top: 6px;
  padding-top: 6px;
  border-top: 1px solid var(--border-color);
}

.conditions-title {
  font-size: 10px;
  color: var(--subtext-color);
  margin-bottom: 4px;
  font-weight: 500;
}

.conditions-list {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.condition-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 10px;
  padding: 3px 6px;
  background-color: var(--quote-bg-color);
  border-radius: 4px;
}

.condition-item.condition-met {
  background-color: rgba(166, 227, 161, 0.1);
  border-left: 2px solid var(--green);
}

.condition-name {
  color: var(--subtext-color);
}

.condition-status {
  font-weight: bold;
  margin-left: 8px;
}

.condition-item.condition-met .condition-status {
  color: var(--green);
}

.condition-item:not(.condition-met) .condition-status {
  color: var(--red);
}

.no-endings {
  margin-top: 12px;
  padding: 12px;
  background-color: var(--quote-bg-color);
  border-radius: 6px;
  text-align: center;
}

.no-endings-text {
  font-size: 12px;
  color: var(--subtext-color);
  font-style: italic;
}
