
.skills-panel {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.panel-title {
  margin: 0 0 8px 0;
  font-size: 16px;
  color: var(--subtext-color);
  border-bottom: 1px solid var(--border-color);
  padding-bottom: 8px;
}

.skill-points-display {
  margin-bottom: 8px;
}

.points-card {
  display: flex;
  align-items: center;
  gap: 12px;
  background: var(--card-bg-color);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 14px 18px;
}

.points-icon {
  font-size: 28px;
}

.points-info {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.points-value {
  font-size: 24px;
  font-weight: 700;
  color: var(--text-color);
}

.points-label {
  font-size: 12px;
  color: var(--subtext-color);
}

.magic-max-upgrade {
  background: var(--card-bg-color);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 16px;
  margin-bottom: 16px;
}

.upgrade-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
  padding-bottom: 8px;
  border-bottom: 1px solid var(--border-color);
}

.upgrade-title {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-color);
}

.current-magic-max {
  font-size: 13px;
  color: var(--subtext-color);
}

.upgrade-controls {
  display: flex;
  gap: 12px;
  margin-bottom: 12px;
}

.input-group {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.input-label {
  font-size: 12px;
  color: var(--subtext-color);
  font-weight: 500;
}

.upgrade-input {
  padding: 8px 12px;
  border: 1px solid var(--border-color);
  border-radius: 6px;
  background: var(--bg-color);
  color: var(--text-color);
  font-size: 14px;
  transition: all 0.2s;
}

.upgrade-input:focus {
  outline: none;
  border-color: var(--blue);
  box-shadow: 0 0 0 3px rgba(137, 180, 250, 0.1);
}

.upgrade-button {
  padding: 8px 16px;
  background: var(--blue);
  border: none;
  border-radius: 6px;
  color: var(--bg-color);
  font-size: 13px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  white-space: nowrap;
}

.upgrade-button:hover:not(.disabled) {
  background: var(--blue);
  opacity: 0.9;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(137, 180, 250, 0.3);
}

.upgrade-button:active:not(.disabled) {
  transform: translateY(0);
}

.upgrade-button.disabled {
  background: var(--border-color);
  color: var(--subtext-color);
  cursor: not-allowed;
}

.upgrade-info {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  color: var(--subtext-color);
  padding-top: 8px;
  border-top: 1px solid var(--border-color);
}

.upgrade-cost {
  color: var(--red);
  font-weight: 500;
}

.upgrade-result {
  color: var(--green);
  font-weight: 500;
}

.skills-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.skill-card {
  background-color: var(--card-bg-color);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 14px;
  transition: all 0.2s;
}

.skill-card:hover {
  border-color: var(--blue);
  box-shadow: 0 2px 8px rgba(137, 180, 250, 0.2);
}

.skill-card.learned {
  border-color: var(--green);
  background: var(--card-bg-color);
}

.skill-card.disabled {
  opacity: 0.6;
}

.skill-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.skill-title {
  display: flex;
  align-items: center;
  gap: 8px;
}

.learned-badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  background-color: var(--green);
  color: var(--bg-color);
  border-radius: 50%;
  font-size: 12px;
  font-weight: bold;
}

.skill-name {
  font-size: 15px;
  font-weight: 600;
  color: var(--text-color);
}

.skill-cost-badge {
  font-size: 11px;
  color: var(--subtext-color);
  background-color: var(--card-bg-color);
  padding: 4px 8px;
  border-radius: 4px;
  border: 1px solid var(--border-color);
}

.skill-description {
  margin: 0 0 12px 0;
  font-size: 13px;
  color: var(--subtext-color);
  line-height: 1.5;
}

.learn-button {
  width: 100%;
  padding: 10px;
  background: var(--blue);
  border: none;
  border-radius: 6px;
  color: var(--bg-color);
  font-size: 13px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.learn-button:hover:not(.disabled) {
  background: var(--blue);
  opacity: 0.9;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(137, 180, 250, 0.3);
}

.learn-button:active:not(.disabled) {
  transform: translateY(0);
}

.learn-button.disabled {
  background: var(--border-color);
  color: var(--subtext-color);
  cursor: not-allowed;
}

.learned-status {
  text-align: center;
  padding: 10px;
  background-color: var(--card-bg-color);
  border-radius: 6px;
  border: 1px solid var(--green);
}

.learned-text {
  color: var(--green);
  font-size: 13px;
  font-weight: 600;
}

.empty-skills {
  text-align: center;
  padding: 24px;
  color: var(--subtext-color);
  font-style: italic;
}

@keyframes skill-learned {
  0% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(166, 227, 161, 0.7);
  }
  50% {
    transform: scale(1.05);
    box-shadow: 0 0 20px 10px rgba(166, 227, 161, 0.4);
  }
  100% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(166, 227, 161, 0);
  }
}

.skill-learned-animation {
  animation: skill-learned 1s ease-in-out;
}
