<template>
  <div class="character-archive">
    <div v-if="Object.keys(allCharacters).length === 0" class="empty-state">
      <p>暂无角色数据</p>
    </div>
    <div v-else class="character-grid">
      <div
        v-for="(character, name) in allCharacters"
        :key="name"
        class="character-card"
        @click="viewCharacter(name)"
      >
        <div class="character-avatar">
          <img
            v-if="character.Avatar?.current"
            :src="character.Avatar.current"
            :alt="name"
            @error="handleImageError"
          />
          <div v-else class="avatar-placeholder">
            <i class="fas fa-user"></i>
          </div>
        </div>
        <div class="character-info">
          <h3 class="character-name">{{ name }}</h3>
          <div class="character-stats">
            <div class="stat-item">
              <i class="fas fa-heart" style="color: var(--pink)"></i>
              <span>{{ character.Favor }}%</span>
            </div>
            <div class="stat-item">
              <i class="fas fa-bolt" style="color: var(--green)"></i>
              <span>{{ character.Magic }}/{{ character.MagicMax }}</span>
            </div>
            <div class="stat-item">
              <i class="fas fa-skull-crossbones" style="color: var(--purple)"></i>
              <span>{{ character.Corruption }}%</span>
            </div>
          </div>
        </div>
        <div class="character-status-badge" :class="getPresenceClass(name)">
          {{ getPresenceText(name) }}
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { computed } from 'vue';
import { useDataStore } from '../store';

const dataStore = useDataStore();
const emit = defineEmits<{
  (e: 'view-character', name: string): void;
}>();

// 获取所有角色
const allCharacters = computed(() => {
  return dataStore.data.角色列表 || {};
});

// 获取在场角色列表
const presentCharacters = computed(() => {
  return dataStore.data.在场角色 || {};
});

// 检查角色是否在场
function isPresent(name: string): boolean {
  return name in presentCharacters.value;
}

// 获取在场状态样式类
function getPresenceClass(name: string): string {
  return isPresent(name) ? 'present' : 'absent';
}

// 获取在场状态文本
function getPresenceText(name: string): string {
  return isPresent(name) ? '在场' : '不在场';
}

// 查看角色详情
function viewCharacter(name: string) {
  emit('view-character', name);
}

// 处理图片加载失败
function handleImageError(event: Event) {
  const img = event.target as HTMLImageElement;
  img.style.display = 'none';
  const placeholder = img.parentElement?.querySelector('.avatar-placeholder');
  if (placeholder) {
    (placeholder as HTMLElement).style.display = 'flex';
  }
}
</script>

